/*
 * Animation Keyframes
 *
 * "Windmill Spinner" "fadeIn"
 */

@keyframes windSpin {
  0% {transform: translate(-50%,-50%) rotate(0deg)}
  100% {transform: translate(-50%,-50%) rotate(360deg)}
}
@-webkit-keyframes windSpin {
  0% {transform: translate(-50%,-50%) rotate(0deg)}
  100% {transform: translate(-50%,-50%) rotate(360deg)}
}
@-moz-keyframes windSpin {
  0% {transform: translate(-50%,-50%) rotate(0deg)}
  100% {transform: translate(-50%,-50%) rotate(360deg)}
}
@-o-keyframes windSpin {
  0% {transform: translate(-50%,-50%) rotate(0deg)}
  100% {transform: translate(-50%,-50%) rotate(360deg)}
}

@-webkit-keyframes fadeIn {
    from{ opacity: 0; }
    to{ opacity: 1; }
}
@-moz-keyframes fadeIn {
    from{ opacity:0; }
    to{ opacity: 1; }
}
@-o-keyframes fadeIn {
    from{ opacity: 0; }
    to{ opacity: 1; }
}
@keyframes fadeIn {
    from{ opacity: 0; }
    to{ opacity: 1; }
}

